<template>
  <div class="admin-account">
      <h1>Admin Account</h1>
      <div class="admin-account-info">
          <div class="info-admin" :class="{ 'show-text': isShow.adminText }" >
              <img :src="setImage" alt="">
              <h3 class="admin-name">{{ userInfo.username }}</h3>
              <h3 class="admin-email" >{{ userInfo.email }}</h3>
              <div class="input-wrapper">
                 <MyInput
                    label="Your Current Password"
                    size="md"
                    type="password"
                    id="currentPassword"
                    bdrColor="white"
                    ref="inputPass"
                    @onInput="checkAdmin($event)"
                    class="input-password"
                 />
              </div>
              <div 
                v-if="verifyError"
                class="error-server">
                    {{ verifyError }}
              </div>
          </div>
          <div 
            class="edit-admin" 
            :class="{ 'show-form' : isShow.adminForm }" 
          >
              <form >
                  <div class="form-username" >
                      <label for="name">Name</label>
                      <input 
                        type="text" 
                        name="name" 
                        id="name"
                        v-model="dataForm.name" 
                        autocomplete="off">
                      <div v-if="error.username.length > 0 " class="error-list">
                          <p 
                            v-for="(err,index) in error.username"
                            :key="index"
                          >
                            {{ err }}
                          </p>
                      </div>
                  </div>
                  <div>
                      <label for="email">Email</label>
                      <input 
                        type="text" 
                        name="email"
                        v-model="dataForm.email" 
                        id="email" 
                        autocomplete="off" >
                      <div v-if="error.email.length > 0 " class="error-list">
                          <p 
                            v-for="(err,index) in error.email"
                            :key="index"
                          >
                            {{ err }}
                          </p>
                      </div>
                  </div>
                  <div>
                      <label for="password">Password</label>
                      <input 
                        type="password" 
                        name="password"
                        v-model="dataForm.pwd" 
                        id="password" 
                        autocomplete="off" 
                      >
                      <div v-if="error.password.length > 0 " class="error-list">
                          <p 
                            v-for="(err,index) in error.password"
                            :key="index"
                          >
                            {{ err }}
                          </p>
                      </div>
                  </div>
                  <div>
                      <label for="confirm-password">Confirm Password</label>
                      <input 
                        type="password" 
                        name="confirm-password" 
                        v-model="dataForm.confirmPwd"
                        id="confirm-password" 
                        autocomplete="off" 
                      >
                      <div v-if="error.confirmPwd.length > 0 " class="error-list">
                          <p 
                            v-for="(err,index) in error.confirmPwd"
                            :key="index"
                          >
                            {{ err }}
                          </p>
                      </div>
                  </div>
                  <div>
                      <label for="file-img">Photo Profile</label>
                      <input 
                        type="file" 
                        name="file-img" 
                        id="file-img" 
                        @change="handleInput"
                        ref="fileInput"
                        autocomplete="off" 
                      >
                      <img :src="setImage" id="img-review"  alt="">
                      <a
                        v-if="showDel"
                        @click="deleteImg"
                        class="btn btn-danger">Delete
                      </a>
                      <div v-if="error.avatar.length > 0 " class="error-list">
                          <p 
                            v-for="(err,index) in error.avatar"
                            :key="index"
                          >
                            {{ err }}
                          </p>
                      </div>
                  </div>
                  <div 
                    v-if="errorServer"
                    class="error-server"
                  >
                    <p>{{ errorServer }}</p>
                  </div>
                  <a
                    role="button"
                    @click="submitForm"
                    class="submit-form"
                  > 
                    Edit
                  </a>
              </form>
          </div>
      </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { updateAdmin,verifyAdmin } from '@/utils/index';

export default {
    name : 'adminAccount',
    components : {
        MyInput : () => import('@/components/MyInputTxt'),
    },
    computed : {
        ...mapState({  userInfo : 'user' }),
        setImage() {
            if( this.userInfo.avatar && this.userInfo.avatar.filename ) return `/img-avatar/${this.userInfo.avatar.filename}`;
            return false;
        },
    },
    data() {
        return {
            isShow : {
                adminText : false,
                adminForm : false,
            },
            dataForm : {
                name : '',
                email : '',
                pwd : '',
                confirmPwd : '',
                avatar : '',
            },
            error : {
                username : [],
                email : [],
                password : [],
                confirmPwd : [],
                avatar : [],
            },
            errorServer : "",
            verifyError : null,
            imgDefault : false,
            showDel : null,
            inputValue : "",
        }
    },
    created() {
        setTimeout( () => {
            this.isShow.adminText = true;
        },700);
        if(this.userInfo.avatar.filename) this.showDel = true;
        this.setForm(this.userInfo);
    },
    methods : {
        handleInput() {
            const file  = this.$refs.fileInput.files[0];
            const img = document.getElementById('img-review');
            img.src = URL.createObjectURL(file);
            this.dataForm.avatar = file;
            img.onload = () => {
                URL.revokeObjectURL(img.src);
            }
            this.showDel = true;
        },

        setForm(user) {
            this.dataForm = {
                name : user.username,
                email : user.email,
            }
        },

        deleteImg() {
            const img = document.getElementById('img-review');
            img.src = "";
            this.dataForm.avatar = null;
            this.showDel = false;
        },
        async submitForm() {
            const isError = this.validateForm(this.dataForm);
            if(isError) return false;
            try {
                const result = await updateAdmin({...this.dataForm,imgDefault : this.imgDefault});
                if(result.error) {
                    return this.errorServer = result.message;
                }
                this.isShow.adminForm = false;
                return result;
            } catch(err) {
                console.log(err);
            }
        },
        validateForm({
            name,
            email,
        }) {
            const err = this.error
            const img = document.getElementById('img-review');
            if( name.length > 0 ) {
                err.username = [];
            } else {
                err.username.push('name is required');  
            }

            if( email.length > 0 ) {
                err.email = [];
            } else {
                err.email.push('email is required');
            } 
            if( encodeURI(this.setImage) == img.src ) this.imgDefault = true;
            // Check if err
            if( err.username.length > 0 ||
                err.email.length > 0 ||
                err.password.length > 0 ||
                err.confirmPwd.length > 0 ||
                err.avatar.length > 0
            ) return true;
            return false;
        },

        async checkAdmin(pass) {
            try {
                this.verifyError = "";
                const { error } = await verifyAdmin(pass);
                if(error) return this.verifyError = 'Password not match'
                this.isShow.adminForm = true;
                this.$refs.inputPass.clearInput();
            } catch(err) {
                console.log(err);
            }
        },


        clearForm() {
            return this.dataForm = {
                name : '',
                email : '',
                pwd : '',
                confirmPwd : '',
                avatar : '',
            }
        }
    },
    watch : {
        "dataForm.confirmPwd" : function(val) {
            if(val != this.dataForm.pwd ) return this.error.confirmPwd[0] = "Password doesn't match";
            return this.error.confirmPwd = [];
        },
        "dataForm.pwd" : function(val) {
            if(val.length <= 6 ) {
                this.error.password[0] = 'Password must be more 6 letters'
            } else {
                this.error.password = [];
            }
            if(val !== this.dataForm.confirmPwd ) {
                this.error.confirmPwd[0] = "Password doesn't match"
            } else {
                this.error.confirmPwd = [];
            }
        }
    }
}
</script>

